                    <table class="table student-data-table">
                      <thead>
                        <tr>
                          <th colspan="2">審稿人</th>
                          <th>已key單</th>
                          <th>ftp抓檔</th>
                        </tr>
                      </thead>
                      <tbody></tbody>
                    </table>
                  </div>
                </div>
              </div>
            </div>
            <div data-dep="digit" class="col-lg-3">
              <div class="card">
                <div class="card-body">
                  <div class="table-responsive">
                    <table class="table student-data-table">
                      <thead>
                        <tr>
                          <th colspan="2">審稿人</th>
